// Listen for messages from the main thread
/* eslint-disable no-restricted-globals */
self.onmessage = (event) => {
  const { row, skills, colors } = event.data;

  // Process the skill matrix data for printing
  const processedSkills = skills.map((skill) => {
    const skillLevel = row[skill.id];
    const chartColors =
      skillLevel === 0
        ? colors[0]
        : skillLevel === 1
        ? colors[1]
        : skillLevel === 2
        ? colors[2]
        : skillLevel === 3
        ? colors[3]
        : colors[4];

    return {
      skillName: skill.skill_name,
      chartColors,
    };
  });

  // Send the processed data back to the main thread
  self.postMessage({ processedSkills, row });
};
